<template>
  <div id="cell">
    <div @click="onClickSelf" class="cell-item">
      <template v-if="toggle">{{ text }}</template>
      <template v-else></template>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Cell',
    props: ['n'],
    data() {
      return {
        toggle: false,
        text: ''

      }
    },
    methods: {
      onClickSelf() {
        if(this.text !== ''){
          return
        }
        this.toggle = !this.toggle
        this.text = this.n % 2 === 0 ? 'X' : 'O'
        this.$emit('click',this.text)
      }
    }
  }
</script>

<style scoped>
  .cell-item {
    width: 65px;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #000;
    font-size: 60px;
  }
</style>