<template>
  <div id="app">

    <div class="container">
      <header class="header">
        <h4>井字棋</h4>
        <span>请点击格子内即可开始</span>
      </header>
      <div class="row">
        <cell @click="onclickCell(0,$event)" :n="n"></cell>
        <cell @click="onclickCell(1,$event)" :n="n"></cell>
        <cell @click="onclickCell(2,$event)" :n="n"></cell>
      </div>
      <div class="row">
        <cell @click="onclickCell(3,$event)" :n="n"></cell>
        <cell @click="onclickCell(4,$event)" :n="n"></cell>
        <cell @click="onclickCell(5,$event)" :n="n"></cell>
      </div>
      <div class="row">
        <cell @click="onclickCell(6,$event)" :n="n"></cell>
        <cell @click="onclickCell(7,$event)" :n="n"></cell>
        <cell @click="onclickCell(8,$event)" :n="n"></cell>
      </div>
      <div class="end">
        <span> {{result}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  import Cell from './components/Cell'

  export default {
    name: 'App',
    data() {
      return {
        n: 0,

        map: [
          [null, null, null],
          [null, null, null],
          [null, null, null]
        ],
        result: ''
      }
    },
    components: {
      Cell
    },
    methods: {
      onclickCell(i, text) {
        this.map[Math.floor(i / 3)][i % 3] = text
        this.n += 1
        this.tell()
      },
      tell() {
        const map = this.map
        for (let i = 0; i < 3; i++) {
          if (
            map[i][0] !== null &&
            map[i][0] === map[i][1] &&
            map[i][1] === map[i][2]
          ) {
            this.result = `${map[i][0]} 赢了！`
          }
        }
        for (let j = 0; j < 3; j++) {
          if (
            map[0][j] !== null &&
            map[0][j] === map[1][j] &&
            map[1][j] === map[2][j]
          ) {
            this.result = ` ${map[0][j]} 赢了！`
          }
        }
        if (
          map[0][0] !== null &&
          map[0][0] === map[1][1] &&
          map[1][1] === map[2][2]
        ) {
          this.result = `${map[0][0]} 赢了！`
        }
        if (
          map[0][2] !== null &&
          map[0][2] === map[1][1] &&
          map[1][1] === map[2][0]
        ) {
          this.result = `${map[0][2]} 赢了！`
        }
      }
    }
  }
</script>

